@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"),
    url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"),
    url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

:root {
  --basic-black: #000000;
  --basic-light-black: #111111;

  --basic-green: #68b738;
  --basic-light-green: #5eaa2f;

  --basic-gray: #f2f2f2;
  --basic-light-gray: #ebebeb;
  --basic-soft-gray: #cdcdcd;
  --basic-darkl-gray: #666666;
  --basic-special-gray: #444444;
  --basic-darkest-gray: #222222;
  --basic-silver: #d9d9d9;
  --basic-light-silver: #eaeaea;
  --basic-soft-silver: #e6e6e6;
  --basic-lightest-gray: #e7e7e7;
  --basic-pink: #ff8282;
  --gray: #bababa;

  --basic-background-gray: #e5e5e5;

  --basic-white: #ffffff;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}


